import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { WhatsAppSetupVM } from "app/admin/models/whatsApp/whatsAppSetupVM";
import { SubscriptionPaymentPlanService } from "app/admin/services/Admin/subscription-payment-plan.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-subscription-plan",
  templateUrl: "./subscription-payment-plan.component.html",
  styleUrls: ["./subscription-payment-plan.component.scss"],
})
export class SubscriptionPaymentPlanComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  itemsCount: number;
  submitted: false;
  displayedColumns: string[] = ["Id", "plan", "price", "actions"];
  dataSource: MatTableDataSource<WhatsAppSetupVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private fb: FormBuilder,
    private authserv: AuthService,
    private dialog: MatDialog,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private subscriptionPaymentPlanService:SubscriptionPaymentPlanService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authserv.getEduCompId());
    this.myForm = this.fb.group({
      price: ["", Validators.required],
      from: [""],
      to: [""]
    });
  }
  get formCtrl(){
    return this.myForm.controls
  }
  getAllPlans(page: number) {}
  getAllSubscriptionPlans(page: number) {
  this.subscriptionPaymentPlanService.getAllSubscriptionPaymentPlans().subscribe((res)=>{

  })
  }
  deleteSubscruptionPlan(id:number) {
    this.subscriptionPaymentPlanService.deleteSubject(id).subscribe((res)=>{

    })

  }
  openDeleateDialog(id:number): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteSubscruptionPlan(id);
      });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
}
