import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { IAssignSubjectToTeacherVM } from "app/admin/models/admin/teachers";
import { TeacherService } from "app/admin/services/Admin/teacher.service";
import {
  IStageDropVM,
  ISubjectByTeacherId,
  IYearDropVM,
} from "app/shared/models/general/general";
import { GeneralService } from "app/shared/services/General/general.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-assign-subject-to-teacher",
  templateUrl: "./assign-subject-to-teacher.component.html",
  styleUrls: ["./assign-subject-to-teacher.component.scss"],
})
export class AssignSubjectToTeacherComponent implements OnInit {
  myForm: FormGroup;
  functionId: number;
  EduCompId: number;
  teacherUserId: number;
  Stages: IStageDropVM[] = [];
  Years: IYearDropVM[] = [];
  Subjects: ISubjectByTeacherId[] = [];
  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    public teacherService: TeacherService,
    public authService: AuthService,
    public generalService: GeneralService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute
  ) {
    route.paramMap.subscribe((params) => {
      this.teacherUserId = Number(params.get("teacherUserId"));
    });
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      stageId: [""],
      yearId: [""],
      subjectIds: [[], Validators.required],
    });
    this.generalService
      .getAllEduStagesList<IStageDropVM[]>()
      .subscribe((res) => {
        this.Stages = res;
      });
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  get subjectIdsCtrl() {
    return this.myForm.get("subjectIds");
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  assignSubjectToTeacher() {
    if (this.subjectIdsCtrl.value.length == 0) {
      this.toastr.warning("Must Choose at least one Subject");
      return;
    }
    let obj = {} as IAssignSubjectToTeacherVM;
    obj.teacherUserId = this.teacherUserId;
    obj.subjectIds = this.subjectIdsCtrl.value;
    this.spinner.show();

    this.teacherService
      .assignSubjectToTeacher<IAssignSubjectToTeacherVM>(obj)
      .subscribe((response: any) => {
        if (response.returnValue == 200) {
          this.toastr.success("Subject added successfully");
          this.myForm.reset();
        }
        this.spinner.hide();
      });
  }

  getEduYearByStageId(stageId: any) {
    this.Years = [];
    this.Subjects = [];
    if (stageId) {
      this.generalService
        .getEduYearByStageId<IYearDropVM[]>(stageId)
        .subscribe((res) => {
          this.Years = res;
        });
    }
  }

  getSubjectsByYearId(YearId: any) {
    this.Subjects = [];
    if (YearId) {
      this.generalService
        .getSubjectsByYearIdNew<ISubjectByTeacherId[]>(YearId) // new api get subjects by yearId
        //  .getSubjectsByYearId<ISubjectByTeacherId[]>(YearId) // Old api get subjects by teachers in year
        .subscribe((res) => {
          this.Subjects = res;
          console.log(res)
        });
    }
  }
}
