import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { IAddTeachersVM, ITeachersVM } from "app/admin/models/admin/teachers";
import { TeacherService } from "app/admin/services/Admin/teacher.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-add-teacher",
  templateUrl: "./add-teacher.component.html",
  styleUrls: ["./add-teacher.component.scss"],
})
export class AddTeacherComponent implements OnInit {
  myForm: FormGroup;
  EduCompId: number;
  submitted: boolean = false;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  ProfilePicture: any = null;
  imgPreview: string;

  displayedColumns: string[] = [
    "#",
    "name",
    "email",
    "mobile",
    "Description",
    "option",
  ];
  dataSource: MatTableDataSource<ITeachersVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  @ViewChild("addBtn", { read: ElementRef }) addBtn: ElementRef;
  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    public authserv: AuthService,
    public teacherService: TeacherService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private GeneralService: GeneralService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }

  ngOnInit(): void {
    this.getAllTeachers();
    this.myForm = this.fb.group(
      {
        teacherUserId: [null],
        name: [
          null,
          [
            Validators.required,
            Validators.pattern(
              /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
            ),
            CustomeValidator.whiteSpace,
          ],
        ],
        email: [null, [Validators.required, Validators.email]],
        mobile: [
          "",
          [
            Validators.required,
            Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
            Validators.minLength(11),
            Validators.maxLength(11),
            CustomeValidator.startsWith,
          ],
        ],
        password: [
          null,
          [
            Validators.required,
            CustomeValidator.whiteSpace,
            Validators.minLength(3),
          ],
        ],
        confirmedPass: [null],
        Description: [""],
      },
      {
        validator: CustomeValidator.mustMatch("password", "confirmedPass"),
      }
    );

    this.EduCompId = Number(this.authserv.getEduCompId());
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });

    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllTeachers() {
    this.GeneralService.getTeachersByEducompId(0).subscribe((res: any) => {
      this.dataSource = new MatTableDataSource(res.lstTeachers.reverse());
      this.dataSource.paginator = this.paginator;
    });
  }

  addEditTeacher() {
    this.submitted = true;
    // if (this.ProfilePicture == null) {
    //   this.toastr.warning("Must Choose Profile Picture");
    //   return;
    // }

    let obj = {} as IAddTeachersVM;
    obj.teacherUserId = this.FormCtrls.teacherUserId?.value;
    obj.name = this.FormCtrls.name?.value;
    obj.email = this.FormCtrls?.email?.value;
    obj.mobile = this.FormCtrls.mobile?.value;
    obj.password = this.FormCtrls.password?.value;
    obj.confirmedPass = this.FormCtrls.confirmedPass?.value;
    obj.Description = this.FormCtrls.Description?.value;
    obj.ProfilePicture = this.ProfilePicture;
    // console.log("obj", obj);
    // console.log("myForm", this.myForm.value);

    if (this.myForm.valid) {
      this.spinner.show();
      this.teacherService
        .addEditTeacher<IAddTeachersVM>(obj)
        .subscribe((response: any) => {
          if (response.returnValue == 1) {
            this.submitted = false;
            this.imgPreview = "";
            this.myForm.reset();
            this.FormCtrls.teacherUserId.setValue(null);
            this.getAllTeachers();
            this.toastr.success("Teacher added successfully");
          } else this.toastr.warning(response.returnString);
          this.spinner.hide();
        });
    }
  }

  getTeachertoEdit(teacher: ITeachersVM) {
    this.myForm.reset();
    this.myForm.patchValue(teacher);
    this.FormCtrls.name.setValue(teacher.teacher_ar_name);
    this.imgPreview = `mozakretyapi${teacher.profilePicture}`;
  }

  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.ProfilePicture = data;
        this.imgPreview = fileReder;
      };
    }
  }
}
