import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import {
  IPostWhatsAppSetupVM,
  IWhatsAppVariablesVM,
} from "app/admin/models/whatsApp/whatsAppSetupVM";
import { WhatsAppSetupService } from "app/admin/services/whatsApp/whatsApp-setup.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-whatsapp-add",
  templateUrl: "./whatsapp-add.component.html",
  styleUrls: ["./whatsapp-add.component.scss"],
})
export class WhatsappAddComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  msgId: number = null;
  EduCompId: number;
  submitted: boolean = false;
  Variables: IWhatsAppVariablesVM[];
  constructor(
    private fb: FormBuilder,
    private authserv: AuthService,
    private msg: MessagesService,
    private WhatsAppService: WhatsAppSetupService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.msgId = Number(params.get("msgId"));
      if (this.msgId) this.getMsgById(this.msgId);
    });
  }

  ngOnInit() {
    //get variables
    this.WhatsAppService.GetAllWhatsAppVariables().subscribe((res: any) => {
      res.list.map((ele) => {
        ele.Name = "{" + ele.Name + "}";
      });
      this.Variables = res.list;
    });

    this.EduCompId = Number(this.authserv.getEduCompId());
    this.myForm = this.fb.group({
      Id: [this.msgId],
      EduCompId: [this.EduCompId, Validators.required],
      Msg_Title: [null, Validators.required],
      Msg_Header: [null, Validators.required],
      Msg_Body: [null, Validators.required],
    });
  }
  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  get FormCtrl() {
    return this.myForm.controls;
  }

  addEditWhatAppMsg() {
    this.submitted = true;
    let body = {} as IPostWhatsAppSetupVM;
    body.Id = this.FormCtrl.Id?.value;
    body.EduCompId = this.EduCompId;
    body.Msg_Body = this.FormCtrl.Msg_Body?.value;
    body.Msg_Header = this.FormCtrl.Msg_Header?.value;
    body.Msg_Title = this.FormCtrl.Msg_Title?.value;
    if (this.myForm.valid) {
      this.spinner.show();
      this.WhatsAppService.addEditWhatAppMsg(body).subscribe((res: any) => {
        this.submitted = false;
        if (res.returnString == "Success") {
          body.Id == null
            ? this.msg.success("whatsApp.msgAdded") //add
            : this.msg.success("whatsApp.msgUpdated"); //update
        } else this.msg.error(res.returnString);
        this.spinner.hide();
      });
    } else this.msg.warning("General.FormNotValid");
  }

  getMsgById(msgId: number) {
    this.WhatsAppService.getWhatsMsgById(msgId).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.myForm.patchValue(res.msg);
      } else this.msg.error(res.returnString);
    });
  }
}
