import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { addPaymentSubscriptionDetails } from 'app/admin/models/admin/subscriptionPaln';
import { environment } from "environments/environment";

@Injectable({
  providedIn: 'root'
})
export class SubscriptionPaymentPlanService {

  constructor(private http: HttpClient) { }

  getAllSubscriptionPlans() {
    return this.http.get(environment.apiURL + "AJAX/getAllSubscriptionTypes");
  }
  getAllSubscriptionPaymentPlans() {
    return this.http.get(environment.apiURL + "AJAX/getallPaymentPlans");
  }

  addEditSubscriptionPaymentPlan(data: addPaymentSubscriptionDetails) {
    return this.http.post(
      `${environment.apiURL}AJAX/addPaymentDetails`,
      data
    );
  }

  deleteSubject(id: number) {
    return this.http.delete(`${environment.apiURL}AJAX/deleteSubscription${id}`);
  }


}
