import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { AnnouncementVM } from "app/admin/models/admin/AnnouncementVM";
import { AnnouncementService } from "app/admin/services/Admin/announcement.service";
import {
  IRowFunctionVM,
  teacherByEduCompId,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { GeneralService } from "app/shared/services/General/general.service";
@Component({
  selector: "app-announcement-index",
  templateUrl: "./announcement-index.component.html",
  styleUrls: ["./announcement-index.component.scss"],
})
export class AnnouncementIndexComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  maxSize = 9;
  itemsCount: any;
  EduCompId: any;
  Teachers: teacherByEduCompId[] = [];
  displayedColumns: string[] = [
    "announcementText",
    "fromDate",
    "toDate",
    "img",
    "actions",
  ];
  dataSource: MatTableDataSource<AnnouncementVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private authserv: AuthService,
    private dialog: MatDialog,
    private GeneralService: GeneralService,
    private announcementService: AnnouncementService,
    private spinner: NgxSpinnerService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      teacherUserId: ["", Validators.required],
    });
    this.getTeachersByEducompId();
  }

  get teacherUserIdCtrl() {
    return this.myForm.get("teacherUserId");
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getTeacherAnnouncement(1);
      this.getTeachersByEducompId();
    });

    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getTeacherAnnouncement(p: number) {
    if (this.teacherUserIdCtrl.value) {
      this.spinner.show();
      this.announcementService
        .getAllAnnouncements(this.teacherUserIdCtrl.value, p)
        .subscribe((res: any) => {
          this.dataSource = new MatTableDataSource(res.list);
          this.itemsCount = res.ItemsCount;
          this.spinner.hide();
        });
    } else {
      this.toastr.error("  ");
    }
  }

  getTeachersByEducompId() {
    this.GeneralService.getTeachersByEducompId(this.EduCompId).subscribe(
      (res: any) => {
        this.Teachers = res.lstTeachers;
        this.teacherUserIdCtrl.setValue(res.lstTeachers[0]?.teacherUserId);
        this.getTeacherAnnouncement(1);
      }
    );
  }

  openDeleateDialog(group: AnnouncementVM): void {
    // this.dialog
    //   .open(DeleteDialogComponent, {
    //     data: {
    //       msg: group.Name,
    //     },
    //   })
    //   .afterClosed()
    //   .subscribe((confirm) => {
    //     if (confirm) this.removeGroup(group);
    //   });
  }
}
