import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { WhatsAppSetupVM } from "app/admin/models/whatsApp/whatsAppSetupVM";
import { WhatsAppSetupService } from "app/admin/services/whatsApp/whatsApp-setup.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-whatsApp-index",
  templateUrl: "./whatsApp-index.component.html",
  styleUrls: ["./whatsApp-index.component.scss"],
})
export class WhatsAppIndexComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  itemsCount: any;
  p: number = 1;
  submitted: false;
  displayedColumns: string[] = [
    "msgId",
    "msgTitle",
    "msgHeader",
    "msgBody",
    "actions",
  ];
  dataSource: MatTableDataSource<WhatsAppSetupVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private fb: FormBuilder,
    private authserv: AuthService,
    private dialog: MatDialog,
    private toastr: ToastrService,
    private WhatsAppService: WhatsAppSetupService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }
  get titleCtrl() {
    return this.myForm.get("Msg_Title");
  }
  ngOnInit() {
    this.EduCompId = Number(this.authserv.getEduCompId());
    this.myForm = this.fb.group({
      Msg_Title: ["", Validators.required],
      page: [""],
      EduCompId: [this.EduCompId],
    });

    this.getAllWhatsAppMsg(1);
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllWhatsAppMsg(1);
    });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllWhatsAppMsg(p: number) {
    this.myForm.controls["page"]?.setValue(p);
    this.p = p;
    this.spinner.show();
    // console.log("form value", this.myForm.value);
    this.WhatsAppService.getAllWhatsAppMsg(this.myForm.value).subscribe(
      (res: any) => {
        this.dataSource = new MatTableDataSource(res.list);
        this.itemsCount = res.count;
        this.spinner.hide();
      }
    );
  }

  openDeleateDialog(whatsApp: WhatsAppSetupVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: whatsApp.msgTitle,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeWhatsApp(whatsApp);
      });
  }

  removeWhatsApp(whatsApp: WhatsAppSetupVM) {
    this.WhatsAppService.RemoveWhatsAppMsg(whatsApp.msgId).subscribe(
      (response) => {
        if (response) {
          //  console.log('deleted: ', response);
          let deletedIndex = this.dataSource.data.findIndex(
            (e) => e.msgId == whatsApp.msgId
          );
          this.dataSource.data.splice(deletedIndex, 1);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.toastr.success("    ");
        }
      }
    );
  }
}
