import { NgxSpinnerService } from "ngx-spinner";
import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { RolesService } from "app/admin/services/roles/roles.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ToastrService } from "ngx-toastr";
import { MatDialog } from "@angular/material/dialog";
import { GeneralService } from "app/shared/services/General/general.service";
import { teacherBySubEduComp } from "app/admin/models/admin/educations";
import { AnnouncementService } from "app/admin/services/Admin/announcement.service";
import { DatePipe } from "@angular/common";
import { IRowFunctionVM } from "app/shared/models/general/general";

@Component({
  selector: "app-announcement-setup",
  templateUrl: "./announcement-setup.component.html",
  styleUrls: ["./announcement-setup.component.css"],
})
export class AnnouncementSetupComponent implements OnInit {
  announceId: number = 0;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  myForm!: FormGroup;
  picturePath?: any;
  EduCompId: any;
  Teachers: teacherBySubEduComp[];
  submitted = false;
  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    public authserv: AuthService,
    private msg: ToastrService,
    private GeneralService: GeneralService,
    private roleService: RolesService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private datePipe: DatePipe,
    private announcementService: AnnouncementService
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.announceId = Number(params.get("announceId"));
      if (this.announceId > 0) {
        this.getAnnounceById(this.announceId);
      }
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }

  getAnnounceById(announceId: number) {
    this.announcementService
      .getAnnounceById(announceId)
      .subscribe((res: any) => {
        console.log("getGroupById", res);
        this.announceId = res.Id;
        this.FormCtrls.Id.setValue(res.Id);
        this.FormCtrls.fromDate.setValue(res.From_Date);
        this.FormCtrls.toDate.setValue(res.To_Date);
        this.FormCtrls.teacherUserId.setValue(res.teacherUserId);
        this.FormCtrls.announcementText.setValue(res.announcement_text);
        this.FormCtrls.announcementPicture.setValue(res.attach_path);
        this.picturePath = `mozakretyapi${res.attach_path}`;
        console.log("picture path", this.picturePath);
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      Id: "",
      announcementText: [],
      announcementPicture: [""],
      fromDate: [, Validators.required],
      toDate: [, Validators.required],
      teacherUserId: [],
      eduCompId: [this.EduCompId],
    });
    this.getTeachersByEducompId(this.EduCompId);
  }

  getTeachersByEducompId(EduCompId: any) {
    if (EduCompId) {
      this.GeneralService.getTeachersByEducompId(EduCompId).subscribe(
        (res: any) => {
          this.Teachers = res.lstTeachers;
          this.FormCtrls.teacherUserId.setValue(
            res.lstTeachers[0]?.teacherUserId
          );
        }
      );
    }
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.FormCtrls.fromDate.value > this.FormCtrls.toDate.value) {
      this.msg.error("       ");
      return;
    }

    if (
      !this.FormCtrls.announcementText.value &&
      !this.FormCtrls.announcementPicture.value
    ) {
      this.msg.error("        ");
      return;
    }
    if (this.announceId == 0) {
      if (this.myForm.valid) {
        this.spinner.show();
        this.FormCtrls.fromDate.setValue(
          this.datePipe.transform(this.FormCtrls.fromDate.value)
        );
        this.FormCtrls.toDate.setValue(
          this.datePipe.transform(this.FormCtrls.toDate.value)
        );
        this.announcementService
          .addAnnouncement(this.myForm.value)
          .subscribe((res: any) => {
            if (res.returnValue != -1) {
              this.msg.success("   ");
              //   this.myForm.reset();
              this.submitted = false;
            } else {
              this.msg.error("   ");
            }

            this.spinner.hide();
          });
      }
    } else {
      if (this.myForm.valid) {
        this.FormCtrls.Id.setValue(this.announceId);
        this.FormCtrls.fromDate.setValue(
          this.datePipe.transform(this.FormCtrls.fromDate.value)
        );
        this.FormCtrls.toDate.setValue(
          this.datePipe.transform(this.FormCtrls.toDate.value)
        );
        this.announcementService
          .addAnnouncement(this.myForm.value)
          .subscribe((res: any) => {
            if (res.returnValue != -1) {
              this.msg.success("   ");
              // this.myForm.reset();
              this.submitted = false;
              // this.returnProfile();
            } else {
              this.msg.error("   ");
            }
            this.spinner.hide();
          });
      }
    }
  }

  DeletePic() {
    this.spinner.show();
    this.announcementService
      .deleteAttach(this.FormCtrls.Id.value)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success("   ");
          this.picturePath = "mozakretyapinull";
        } else this.msg.warning(res);
        this.spinner.hide();
      });
  }

  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.FormCtrls.announcementPicture.setValue(data);
        this.picturePath = fileReder;
      };
    }
  }
}
